import sqlite3InitModule from '@sqlite.org/sqlite-wasm';
console.log('This is worker.js');

const log = (...args) => console.log(...args);
const error = (...args) => console.error(...args);

const start = function (sqlite3, sql) {

  const readDatabase = (arrayBuffer) => {
    const bytes = new Uint8Array(arrayBuffer);
    const p = sqlite3.wasm.allocFromTypedArray(bytes);
    const db = new sqlite3.oo1.DB();
    sqlite3.capi.sqlite3_deserialize(db.pointer, "main", p, bytes.length, bytes.length, sqlite3.capi.SQLITE_DESERIALIZE_FREEONCLOSE | sqlite3.capi.SQLITE_DESERIALIZE_RESIZEABLE);
    return db;
  };

  (async function () {
    log('Running SQLite3 version', sqlite3.version.libVersion);

    const dataPromise = await fetch(`${process.env.REACT_APP_BASE_URL}/ssdb.sqlite3`).then(res => res.arrayBuffer());
    const u8array = new Uint8Array(dataPromise);
    const db = readDatabase(u8array);

    log("transient db =", db.filename);
    const resultRows = [];

    try {
      db.exec({
        sql: sql,
        callback: (row) => {
          resultRows.push(row);
        },
      });

    } finally {
      db.close();
    }

    postMessage(resultRows);
  })();
};

onmessage = (e) => {
  console.log('Message received from main script');
  if (e.data.sql == null || e.data.sql == "") return;

  const sql = e.data.sql;

  log('Loading and initializing SQLite3 module...');
  sqlite3InitModule({ print: log, printErr: error, }).then((sqlite3) => {
    log('Done initializing. Running demo...');
    try {
      start(sqlite3, sql);
    } catch (err) {
      error(err.name, err.message);
    }
  });
}
